import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Navigation from "../Navigation";
import Footer from "../Footer";
import azure_visualization_image from "./images/azure_visualization.jpg";
import Prism from "prismjs";
import "prismjs/themes/prism-twilight.css";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post19() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["azure", "function", "app-service", "sigma-js"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
    Prism.hooks.add("before-highlight", function (env) {
      env.code = env.element.innerText;
    });
    Prism.highlightAll();
  }, [location]);

  return (
    <div className={themeMode === "dark" ? "dark" : "light"}>
      <Navigation
        themeMode={themeMode}
        onThemeChange={toggleThemeMode}
      ></Navigation>
      <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
        <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
          <div className="mt-16 mb-4 flex justify-items-start content">
            <p className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
              Visualize Azure Groups Membership
            </p>
            <div className="line-break"></div>
            <span className="flex items-center">
              <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
              <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                Feb 1, 2025
              </time>
            </span>
            <div className="line-break"></div>
            <div className="articleBody">
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Microsoft Entra Admin Center is a portal used by administrators
                to manage Microsoft Entra ID Users and Groups. Over time, teams
                within organizations evolve; for example, team members may
                change, and groups may be reshaped. It can be useful in a large
                organization to have a full picture of which members are
                assigned to specific groups, and how the groups evolved.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Starting from the use case just described, I created a tool for
                visualizing the Groups membership using a network graph. A web
                application shows the network graph from historical data of
                users and groups stored in a Storage Account. The web
                application retrieves the data based on the selected date on a
                Calendar widget.
              </p>
              <div className="leading-7 mt-4 text-center w-full image-container">
                <img
                  src={azure_visualization_image}
                  alt="Azure Visualization"
                ></img>
              </div>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                One advantage of using a network graph is that empty groups and
                unassigned users can be easily identified. In addition, the
                network graph can visualize complex groups relationships.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The users and groups membership data stored in the Storage
                Account is created by a PowerShell script running in one Azure
                Function App. The automation solution runs once per day, and it
                requires to use one Azure App Registration (Principal Account)
                for retrieving the users and groups information from Microsoft
                Entra ID.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                I developed the web application using React JS, and I used the
                React-Sigma library [1] for generating the network graph.
                React-Sigma uses graphology [2] for handling the graph data
                model, and Sigma.js [3] for handling the graph rendering and the
                user interactions. The web application is packaged as a Docker
                image, stored in one Azure Container Registry, and runs in one
                Azure App Service.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The GitHub repository of the project is the following:
                <br />
                <a
                  className="underline"
                  href="https://github.com/sitMCella/azure-visualization"
                  target="_blank"
                  rel="noreferrer"
                >
                  sitMCella/azure-visualization
                </a>
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                I decided to use the ForceAtlas2 algorithm in Graphology [4] in
                order to aggregate the users closer to their respective groups.
                Random values are assigned to the node attributes <code>x</code>
                and <code>y</code>, so a refresh of the page will create the
                graph with a different shape. When the user selects a node, the
                node color changes based on the type of node (user or group),
                and the same happens for the directly connected nodes and the
                edges.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The project makes use of Terraform for provisioning the
                resources in Azure. A specific resource in the Terraform project
                is used for building and pushing the Docker image in one Azure
                Container Registry using a shell script, but the script can also
                be executed manually. All of the Azure resources make use of
                private endpoints and service endpoints with firewall rules, for
                restricting the access and enhancing the security.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The solution can be extended to cover additional use cases, as
                an example to visualize RBAC role direct assignments. In this
                project I wanted to showcase a possible technical implementation
                using a cloud native solution, but the package and deployment of
                the application can be adapded based on the requirements.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                [1]{" "}
                <a
                  className="underline"
                  href="https://sim51.github.io/react-sigma/"
                  target="_blank"
                  rel="noreferrer"
                >
                  react-sigma
                </a>
                <br />
                [2]{" "}
                <a
                  className="underline"
                  href="https://graphology.github.io/"
                  target="_blank"
                  rel="noreferrer"
                >
                  graphology
                </a>
                <br />
                [3]{" "}
                <a
                  className="underline"
                  href="https://www.sigmajs.org/"
                  target="_blank"
                  rel="noreferrer"
                >
                  Sigma.js
                </a>
                <br />
                [4]{" "}
                <a
                  className="underline"
                  href="https://graphology.github.io/standard-library/layout-forceatlas2.html"
                  target="_blank"
                  rel="noreferrer"
                >
                  forceatlas2
                </a>
              </p>
            </div>
          </div>
          <div className="mt-8 flex items-center text-left">
            <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
            {tags.map((item, index) => (
              <span
                key={`tag-${index}`}
                className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
              >
                {item}
              </span>
            ))}
          </div>
        </div>
        <div className="mb-16"></div>
        <div></div>
      </div>
      <Footer></Footer>
    </div>
  );
}

export default Post19;
