import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Navigation from "./Navigation";
import Footer from "./Footer";
import "./Profile.css";

function Profile() {
  const location = useLocation();
  const themeModeInitial = location.state
    ? location?.state?.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
  }, [location]);

  return (
    <div className={themeMode === "dark" ? "dark" : "light"}>
      <Navigation
        page="profile"
        themeMode={themeMode}
        onThemeChange={toggleThemeMode}
      ></Navigation>
      <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
        <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
          <div className="mt-4 flex justify-items-start content relative">
            <p className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
              About me
            </p>
            <div className="line-break"></div>
            <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
              My name is Marco Cella, I am based in Koeln, Germany.
              <br />I am passionate about software development, Kubernetes, and
              cloud infrastructures, with 15 years of experience in the IT
              industry.
            </p>
            <div className="line-break"></div>
            <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
              I decided to create this blog to share my experience and hopefully
              some useful tips on different technologies and frameworks.
            </p>
          </div>
          <div className="mt-4 flex justify-items-start content relative">
            <p className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
              Elsewhere
            </p>
            <div className="line-break"></div>
            <p className="mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert">
              <a
                className="underline"
                href="https://www.linkedin.com/in/marco-cella-48391a5b"
                target="_blank"
                rel="noreferrer"
              >
                Linkedin
              </a>
            </p>
            <div className="line-break"></div>
            <p className="mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert">
              <a
                className="underline"
                href="https://github.com/sitMCella"
                target="_blank"
                rel="noreferrer"
              >
                GitHub
              </a>
            </p>
            <div className="line-break"></div>
            <p className="mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert">
              <a
                className="underline"
                href="https://mastodon.social/@marco_cella"
                target="_blank"
                rel="noreferrer"
              >
                Mastodon
              </a>
            </p>
          </div>
        </div>
        <div></div>
      </div>
      <Footer></Footer>
    </div>
  );
}

export default Profile;
