import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Navigation from "../Navigation";
import Footer from "../Footer";
import Prism from "prismjs";
import "prismjs/themes/prism-twilight.css";
import "prismjs/components/prism-java";
import "prismjs/components/prism-sql";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post24() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["postgresql", "triggers"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
    Prism.hooks.add("before-highlight", function (env) {
      env.code = env.element.innerText;
    });
    Prism.highlightAll();
  }, [location]);

  return (
    <div className={themeMode === "dark" ? "dark" : "light"}>
      <Navigation
        themeMode={themeMode}
        onThemeChange={toggleThemeMode}
      ></Navigation>
      <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
        <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
          <div className="mt-16 mb-4 flex justify-items-start content">
            <p className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
              PostgreSQL Triggers and notify/listen
            </p>
            <div className="line-break"></div>
            <span className="flex items-center">
              <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
              <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                May 25, 2025
              </time>
            </span>
            <div className="line-break"></div>
            <div className="articleBody">
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Change Data Capture (CDC) in PostgreSQL is the process for
                identifying and tracking changes made to the database in
                near-real time. This helps ensure that the data is kept
                synchronised on downstream systems. PostgreSQL supports Change
                Data Capture using several approaches, including triggers,
                polling queries, logical replication, and write-ahead logs
                (WAL). In this article, I will focus on the triggers and
                notify/listen mechanism.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Triggers in PostgreSQL are used to respond to DML events —
                INSERT, UPDATE, and DELETE — and are executed on table row
                changes [1]. The notify/listen mechanism is used to trigger
                application-level code in response to database events [2]. When
                combined, these two approaches can form a simple event-driven
                architecture, enabling applications to receive notifications in
                near real time.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                I recently worked on a "radar" application that allows devices
                to send signals about nearby moving objects to a central hub. A
                Spring WebFlux reactive application connects the frontend web
                interface to the backend infrastructure via a data stream. This
                reactive application is a separate backend component from the
                central hub, specifically tailored for streaming data from the
                backend to the frontend. One key requirement was for the
                reactive application to receive near real-time notifications
                whenever the hub stores a new event.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The GitHub repository of the project is the following:
                <br />
                <a
                  className="underline"
                  href="https://github.com/sitMCella/radar"
                  target="_blank"
                  rel="noreferrer"
                >
                  sitMCella/radar
                </a>
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Several considerations influenced my choice of an event-based
                mechanism: the solution needed to be lightweight with minimal
                impact on database performance, notifications had to be near
                real-time, and the reactive application could tolerate
                occasional missed notifications from the hub.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                In the remainder of this article, I will explain how to
                configure triggers and the listen/notify mechanism in
                PostgreSQL, as well as how to integrate it with a Spring WebFlux
                application.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                First of all create a trigger in the PostgreSQL initialization
                script. The following is a trigger that responds to INSERT
                operations on the "signals" table:
              </p>
              <pre className="mt-4 text-left h-68 w-full">
                <code className="language-sql">
                  CREATE TRIGGER notify_signals
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;AFTER INSERT
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;ON signals
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;FOR EACH ROW
                  <br />
                  EXECUTE PROCEDURE notify_signals_event();;
                </code>
              </pre>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The function <code>notify_signals_event</code> is defined as:
              </p>
              <pre className="mt-4 text-left h-68 w-full">
                <code className="language-sql">
                  CREATE OR REPLACE FUNCTION notify_signals_event() RETURNS
                  TRIGGER AS
                  <br />
                  $$
                  <br />
                  BEGIN
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;PERFORM
                  pg_notify('signals_notification', json_build_object(
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;'id', NEW.id,
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;'creationtime',
                  to_char(NEW.creationtime, 'YYYY-MM-DD"T"HH24:MI:SS.US"Z"'),
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;'deviceId', NEW.device_id,
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;'objId', NEW.obj_id,
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;'latitude', NEW.latitude,
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;'longitude', NEW.longitude
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;)::text);
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;RETURN NULL;
                  <br />
                  END;
                  <br />
                  $$
                  <br />
                  LANGUAGE plpgsql;;
                </code>
              </pre>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The <code>pg_notify</code> function takes the channel name as
                its first argument and a string payload as the second. In the{" "}
                <code>notify_signals_event</code> function, the field
                "creationtime" is converted from a timestamp to a formatted
                string before being included in the payload.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Next configure the Spring WebFlux application to listen to the
                PostgreSQL events. Spring WebFlux uses the r2dbc-postgresql
                library to connect to the PostgreSQL database.
              </p>
              <pre className="mt-4 text-left h-96 w-full">
                <code className="language-java">
                  import io.r2dbc.postgresql.api.PostgresqlConnection;
                  <br />
                  import io.r2dbc.spi.ConnectionFactory;
                  <br />
                  <br />
                  @Service
                  <br />
                  @EnableR2dbcRepositories
                  <br />
                  public class SignalService &#123;
                  <br />
                  <br />
                  &nbsp;&nbsp;private final PostgresqlConnection connection;
                  <br />
                  <br />
                  &nbsp;&nbsp;SignalService(ConnectionFactory connectionFactory)
                  &#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;this.connection =
                  Mono.from(connectionFactory.create())
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;.cast(PostgresqlConnection.class).block();
                  <br />
                  &nbsp;&nbsp;&#125;
                  <br />
                  <br />
                  &nbsp;&nbsp;@PostConstruct
                  <br />
                  &nbsp;&nbsp;private void postConstruct() &#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;connection
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;.createStatement("LISTEN
                  signals_notification")
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;.execute()
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;.flatMap(PostgresqlResult::getRowsUpdated)
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;.subscribe();
                  <br />
                  &nbsp;&nbsp;&#125;
                  <br />
                  <br />
                  &nbsp;&nbsp;@PreDestroy
                  <br />
                  &nbsp;&nbsp;private void preDestroy() &#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;connection.close().subscribe();
                  <br />
                  &nbsp;&nbsp;&#125;
                  <br />
                  &#125;
                </code>
              </pre>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Note that you cannot inject directly the PostgresqlConnection in
                the service, instead you need to inject the ConnectionFactory
                and then create the PostgresqlConnection. The service can listen
                to the PostgreSQL events using:
              </p>
              <pre className="mt-4 text-left h-68 w-full">
                <code className="language-java">
                  public Flux&lt;Signal&gt; streamSignals() &#123;
                  <br />
                  &nbsp;&nbsp;return connection
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;.getNotifications()
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;.onBackpressureBuffer(1000, dropped
                  -&gt; logger.warn("Dropped notification: " + dropped))
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;.map(notification -&gt; &#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;try &#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;return
                  objectMapper.readValue(notification.getParameter(),
                  Signal.class);
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125; catch
                  (JsonProcessingException e) &#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;logger.error("Cannot
                  send signal", e);
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;return null;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&#125;);
                  <br />
                  &#125;
                </code>
              </pre>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The backpressure buffer operator controls how buffer overflows
                are handled when the subscriber cannot keep up with the database
                notification stream. It helps limit the risk of unhandled events
                in a reactive application.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The trigger and listener/notify mechanisms work seamlessly with
                the Spring WebFlux application, allowing it to handle near
                real-time events even under heavy load.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                [1]{" "}
                <a
                  className="underline"
                  href="https://www.postgresql.org/docs/17/trigger-definition.html"
                  target="_blank"
                  rel="noreferrer"
                >
                  Triggers
                </a>
                <br />
                [2]{" "}
                <a
                  className="underline"
                  href="https://www.postgresql.org/docs/17/sql-listen.html"
                  target="_blank"
                  rel="noreferrer"
                >
                  Listen
                </a>
              </p>
            </div>
          </div>
          <div className="mt-8 flex items-center text-left">
            <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
            {tags.map((item, index) => (
              <span
                key={`tag-${index}`}
                className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
              >
                {item}
              </span>
            ))}
          </div>
        </div>
        <div className="mb-16"></div>
        <div></div>
      </div>
      <Footer></Footer>
    </div>
  );
}

export default Post24;
