import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Navigation from "../Navigation";
import Footer from "../Footer";
import Prism from "prismjs";
import "prismjs/themes/prism-twilight.css";
import "prismjs/components/prism-javascript";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post23() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["sse", "web-worker", "rxjs", "react"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
    Prism.hooks.add("before-highlight", function (env) {
      env.code = env.element.innerText;
    });
    Prism.highlightAll();
  }, [location]);

  return (
    <div className={themeMode === "dark" ? "dark" : "light"}>
      <Navigation
        themeMode={themeMode}
        onThemeChange={toggleThemeMode}
      ></Navigation>
      <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
        <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
          <div className="mt-16 mb-4 flex justify-items-start content">
            <p className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
              Server-Sent Events in React
            </p>
            <div className="line-break"></div>
            <span className="flex items-center">
              <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
              <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                Apr 27, 2025
              </time>
            </span>
            <div className="line-break"></div>
            <div className="articleBody">
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Server-Sent Events (SSE) is a unidirectional, event-based
                communication protocol that allows a server to send data to a
                client over HTTP using a persistent connection. WebSocket is
                another technology that establishes a communication stream
                between the server and the client. Unlike Server-Sent Events,
                WebSocket enables bidirectional communication and is not based
                on the HTTP protocol.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The HTML specification includes support for Server-Sent Events
                and introduces the EventSource API, which is natively supported
                by most modern browsers. The EventSource API requires the server
                to send responses using the MIME type text/event-stream, and the
                messages are streamed as UTF-8 encoded text data. To open a
                connection with the server, you just need to create an
                EventSource object and then listen for the message events.
              </p>
              <pre className="mt-4 text-left h-68 w-full">
                <code className="language-javascript">
                  const eventSource = new EventSource('/api/devices')
                  <br />
                  <br />
                  eventSource.onmessage = (event) =&gt; &#123;
                  <br />
                  &nbsp;&nbsp;const device = JSON.parse(event.data)
                  <br />
                  &nbsp;&nbsp;if (!devices.some((item) =&gt; device.id ===
                  item.id)) &#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;setDevices((prevDevices) =&gt;
                  [...prevDevices, device])
                  <br />
                  &nbsp;&nbsp;&#125;
                  <br />
                  &#125;
                  <br />
                  <br />
                  eventSource.onerror = (err) =&gt; &#123;
                  <br />
                  &nbsp;&nbsp;console.error('Devices SSE error:', err)
                  <br />
                  &#125;
                </code>
              </pre>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                I used the EventSource API to process a stream of data from a
                React web application, the GitHub repository of the project is
                the following:
                <br />
                <a
                  className="underline"
                  href="https://github.com/sitMCella/radar"
                  target="_blank"
                  rel="noreferrer"
                >
                  sitMCella/radar
                </a>
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The web application uses React Three Fiber [1] to display a
                radar simulation within a canvas, rendering a grid with points
                that represent the positions of a set of devices and the nearby
                objects they detect. The main goal of the web application is to
                display the positions of the detected objects in near real-time.
                In order to achieve this goal, I developed a reactive backend,
                subscribed to the data stream using EventSource, and processed
                the received messages in the React application.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The initial version of the application handled EventSource
                messages on the browser’s main thread. I noticed that the user
                experience degraded as an increasing number of messages were
                processed from the EventSource in short time intervals. With
                Server-Sent Events the client has not way to notify the server
                to slow down, so in order to handle the pressure from the
                server, I created a Web Worker to handle the EventSource
                messages in a separate thread.
              </p>
              <pre className="mt-4 text-left h-68 w-full">
                <code className="language-javascript">
                  const eventSource = new EventSource('/api/devices')
                  <br />
                  <br />
                  eventSource.onmessage = (event) =&gt; &#123;
                  <br />
                  &nbsp;&nbsp;postMessage(event.data)
                  <br />
                  &#125;
                  <br />
                  <br />
                  eventSource.onerror = (err) =&gt; &#123;
                  <br />
                  &nbsp;&nbsp;postMessage(&#123; error: 'SSE error', details:
                  err &#125;)
                  <br />
                  &#125;
                </code>
              </pre>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                In addition to the Web Worker, I also used the RxJS library to
                handle the asynchronous data stream, particularly because it
                provides a set of operators for rate limiting and buffering. The
                Web Worker is initialized within the RxJS Observable, and the
                messages it sends are forwarded to the RxJS subscriber. I have
                instrumented the Observable with the <i>bufferTime</i> [2]
                operator, used to batch the data emissions from a possible
                high-frequency observable. The <i>bufferTime</i> operator
                accumulates values from the source observable in an array over a
                specified time duration before emitting the buffered array.
              </p>
              <pre className="mt-4 text-left h-96 w-full">
                <code className="language-javascript">
                  const [devices, setDevices] = useState([])
                  <br />
                  <br />
                  const createDevicesObservable = () =&gt; &#123;
                  <br />
                  &nbsp;&nbsp;return new Observable((subscriber) =&gt; &#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;const worker = createDevicesWorker()
                  <br />
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;worker.onmessage = (w) =&gt; &#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;if (w.data?.error) &#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;subscriber.error(w.data)
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;return
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;subscriber.next(w.data)
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&#125;
                  <br />
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;return () =&gt; &#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;worker.terminate()
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&#125;
                  <br />
                  &nbsp;&nbsp;&#125;)
                  <br />
                  &#125;
                  <br />
                  <br />
                  useEffect(() =&gt; &#123;
                  <br />
                  &nbsp;&nbsp;const devicesObservable =
                  createDevicesObservable().pipe(
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;bufferTime(200)
                  <br />
                  )
                  <br />
                  <br />
                  &nbsp;&nbsp;let updatedDevices = &#91;&#93;
                  <br />
                  <br />
                  &nbsp;&nbsp;const devicesSubscription =
                  devicesObservable.subscribe(&#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;next: (batch) =&gt; &#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;batch.forEach((data) =&gt;
                  &#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;const device =
                  JSON.parse(data)
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;if
                  (!updatedDevices.some((item) =&gt; device.id === item.id))
                  &#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;updatedDevices
                  = &#91;...updatedDevices, device&#93;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&#125;)
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;setDevices(updatedDevices)
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&#125;,
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;error: (err) =&gt;
                  console.error('Devices SSE error:', err),
                  <br />
                  &nbsp;&nbsp;&#125;)
                  <br />
                  &#125;, &#91;&#93;)
                </code>
              </pre>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                To update the React UI at a sustainable rate, it's important to
                call <code>setState</code> only at the end of each batch
                execution. Additionally, the React Hook <i>useMemo</i> [3] helps
                to ensure that the React UI re-renders the components only when
                the <i>useState</i> array is actually modified.
              </p>
              <pre className="mt-4 text-left h-68 w-full">
                <code className="language-javascript">
                  const renderedDevices = useMemo(() =&gt; &#123;
                  <br />
                  &nbsp;&nbsp;return devices.map((device, index) =&gt; (
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&lt;Device key=&#123;index&#125;
                  device=&#123;device&#125; /&gt;
                  <br />
                  &nbsp;&nbsp;))
                  <br />
                  &#125;, &#91;devices&#93;)
                  <br />
                  <br />
                  return (
                  <br />
                  &nbsp;&nbsp;&lt;div&gt;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&#123;renderedDevices&#125;
                  <br />
                  &nbsp;&nbsp;&lt;/div&gt;
                  <br />)
                </code>
              </pre>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The new version of the web application is significantly more
                responsive, and React Three Fiber accurately renders the
                positions of devices and objects within the canvas in near
                real-time, even under heavy server load.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                [1]{" "}
                <a
                  className="underline"
                  href="https://r3f.docs.pmnd.rs/getting-started/introduction"
                  target="_blank"
                  rel="noreferrer"
                >
                  React Three Fiber
                </a>
                <br />
                [2]{" "}
                <a
                  className="underline"
                  href="https://www.learnrxjs.io/learn-rxjs/operators/transformation/buffertime"
                  target="_blank"
                  rel="noreferrer"
                >
                  RxJS buffertime
                </a>
                <br />
                [3]{" "}
                <a
                  className="underline"
                  href="https://react.dev/reference/react/useMemo"
                  target="_blank"
                  rel="noreferrer"
                >
                  React useMemo
                </a>
              </p>
            </div>
          </div>
          <div className="mt-8 flex items-center text-left">
            <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
            {tags.map((item, index) => (
              <span
                key={`tag-${index}`}
                className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
              >
                {item}
              </span>
            ))}
          </div>
        </div>
        <div className="mb-16"></div>
        <div></div>
      </div>
      <Footer></Footer>
    </div>
  );
}

export default Post23;
