import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Navigation from "../Navigation";
import Footer from "../Footer";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post4() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["blog", "react", "css"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
  }, [location]);

  return (
    <div className={themeMode === "dark" ? "dark" : "light"}>
      <Navigation
        themeMode={themeMode}
        onThemeChange={toggleThemeMode}
      ></Navigation>
      <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
        <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
          <div className="mt-16 mb-4 flex justify-items-start content">
            <p className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
              Blog Template
            </p>
            <div className="line-break"></div>
            <span className="flex items-center">
              <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
              <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                Aug 24, 2024
              </time>
            </span>
            <div className="line-break"></div>
            <div className="articleBody">
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                I released the template of the web application I developed for
                this web site.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                You can find the code in the following GitHub repository:
                <br />
                <a
                  className="underline"
                  href="https://github.com/sitMCella/blog-template"
                  target="_blank"
                  rel="noreferrer"
                >
                  sitMCella/blog-template
                </a>
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The web application uses a custom blog design and is built using
                React and Tailwind CSS. In addition, the web application uses
                Prism.js to display the code snippets. The React web application
                makes use of the Single Page Application model.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The React application has been adjusted for the single page
                application to be supported in GitHub Pages [1].
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                A key design choice for this web application is its minimalistic
                style. The navigation bar at the top of the page features
                concise menu options, promoting ease of use and a clean
                interface. The Blog page displays posts using simple, card-based
                layouts that include the article's headline, publication date,
                excerpt, and relevant tags. Additionally, a search bar is
                provided to filter articles based on tags. Users can begin
                typing a keyword into the text box and then select a matching
                tag from the dropdown menu.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The article pages maintain a minimalist aesthetic, incorporating
                panels and code snippets to enhance readability and structure.
                Links and references may appear either inline within the article
                or at the bottom of the page, offering flexibility in how
                supplementary information is presented.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                In future I will add new features (for example the dark mode),
                and I will apply changes on some components of the pages.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                [1]{" "}
                <a
                  className="underline"
                  href="https://github.com/rafgraph/spa-github-pages"
                  target="_blank"
                  rel="noreferrer"
                >
                  spa-github-pages
                </a>
              </p>
            </div>
          </div>
          <div className="mt-8 flex items-center text-left">
            <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
            {tags.map((item, index) => (
              <span
                key={`tag-${index}`}
                className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
              >
                {item}
              </span>
            ))}
          </div>
        </div>
        <div className="mb-16"></div>
        <div></div>
      </div>
      <Footer></Footer>
    </div>
  );
}

export default Post4;
