import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Navigation from "../Navigation";
import Footer from "../Footer";
import Prism from "prismjs";
import "prismjs/themes/prism-twilight.css";
import "prismjs/components/prism-bash";
import "prismjs/components/prism-yaml";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post22() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["azure", "kubernetes", "prometheus"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
    Prism.hooks.add("before-highlight", function (env) {
      env.code = env.element.innerText;
    });
    Prism.highlightAll();
  }, [location]);

  return (
    <div className={themeMode === "dark" ? "dark" : "light"}>
      <Navigation
        themeMode={themeMode}
        onThemeChange={toggleThemeMode}
      ></Navigation>
      <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
        <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
          <div className="mt-16 mb-4 flex justify-items-start content">
            <p className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
              Prometheus Scrape Metrics
            </p>
            <div className="line-break"></div>
            <span className="flex items-center">
              <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
              <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                Mar 22, 2025
              </time>
            </span>
            <div className="line-break"></div>
            <div className="articleBody">
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Prometheus is a monitoring and alerting toolkit designed to
                collect, process, and store metrics as time series data, playing
                a crucial role in infrastructure observability. Prometheus
                consists of multiple components, but in this article, I will
                focus on its metrics scraping functionality.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Prometheus collects metrics from instrumented jobs using a
                pull-based model, either directly or through an intermediary
                push gateway for short-lived jobs. To enable this, applications
                must expose an http(s) endpoint containing metrics in Prometheus
                format. Prometheus then periodically scrapes these endpoints to
                gather and store the metrics.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                I will describe three different approaches for configuring
                Prometheus to scrape metrics, using Kubernetes Helm Charts
                deployed in one Azure Kubernetes Service resource.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The GitHub repository of the project is the following:
                <br />
                <a
                  className="underline"
                  href="https://github.com/sitMCella/prometheus-metrics"
                  target="_blank"
                  rel="noreferrer"
                >
                  sitMCella/prometheus-metrics
                </a>
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The project provides one Helm Chart with name "metrics", with
                two dependencies: Kube Prometheus Stack and Grafana Tempo.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Kube Prometheus Stack [1] is an Helm Chart pre-packaged with
                many different components, such as Prometheus, Prometheus
                Operator, and Alert Manager.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                For all three scenarios, Grafana Tempo is used as the example
                application instrumented to send metrics to Prometheus. In this
                project I used the Tempo deployment in monolithic mode [2]. It's
                important to notice that Kube Prometheus Stack is pre-configured
                to collect metrics from all Kubernetes components, so the
                Prometheus Targets page will contain many other instances
                alongside Grafana Tempo.
              </p>
              <p className="font-semibold leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                ServiceMonitor
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Prometheus Operator [3] is a Kubernetes Operator that provides
                Kubernetes native deployment and management of Prometheus and
                related monitoring components. The Prometheus Operator provides
                a set of Custom Resource Definitions (CRDs) to simplify the
                target configuration in Kubernetes, reducing the need for manual
                Prometheus configuration. Specifically, the ServiceMonitor CRD
                is used to define which services should be scraped for metrics
                within Kubernetes.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                In order for Prometheus Operator to scrape a target, the
                ServiceMonitor resource must be configured with the matching
                label defined in Prometheus.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Run the following command to verify the label applied in
                Prometheus:
              </p>
              <pre className="mt-4 text-left h-68 w-full">
                <code className="language-bash">
                  kubectl describe
                  prometheus/metrics-kube-prometheus-st-prometheus -n metrics
                </code>
              </pre>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The Prometheus Operator is configured with:
              </p>
              <pre className="mt-4 text-left h-68 w-full">
                <code className="language-yaml">
                  Service Monitor Selector:
                  <br />
                  &nbsp;&nbsp;Match Labels:
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;Release: metrics
                </code>
              </pre>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Within the ServiceMonitor resource you need to specify the
                Kubernetes labels that the Prometheus Operator can use to
                identify the Kubernetes Service which in turn identifies the
                Pods of the application to monitor. The Grafana Tempo Helm Chart
                is configured with the ServiceMonitor resource for the Tempo
                service, so you just need to specify the matching label for
                instructing Prometheus to scrape the target.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The following is the configuration applied to Grafana Tempo:
              </p>
              <pre className="mt-4 text-left h-68 w-full">
                <code className="language-yaml">
                  tempo:
                  <br />
                  &nbsp;&nbsp;serviceMonitor:
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;enabled: true
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;interval: 15s
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;additionalLabels:
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;release: metrics
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;annotations: &#123;&#125;
                </code>
              </pre>
              <p className="font-semibold leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Additional Scrape Configs
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The second approach is to configure PrometheusSpec in the Kube
                Prometheus Stack with additional scrape configurations:
              </p>
              <pre className="mt-4 text-left h-68 w-full">
                <code className="language-yaml">
                  kube-prometheus-stack:
                  <br />
                  &nbsp;&nbsp;prometheus:
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;prometheusSpec:
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;additionalScrapeConfigs:
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;- job_name:
                  'grafana-tempo-additional'
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;scrape_interval:
                  15s
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;static_configs:
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;-
                  targets:
                  &#91;'grafana-tempo.metrics.svc.cluster.local:3100'&#93;
                </code>
              </pre>
              <p className="font-semibold leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                ScrapeConfig
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The Prometheus Operator provides also the ScrapeConfig custom
                resource. This approach is commonly used to scrape targets
                external to the Kubernetes cluster that are not discoverable via
                ServiceMonitor or PodMonitor.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The scrapeConfigSelector field in the Prometheus resource must
                be configured with a set of label selectors that match the
                labels applied to the ScrapeConfig resources.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                In the project, Prometheus has been configured as follows:
              </p>
              <pre className="mt-4 text-left h-68 w-full">
                <code className="language-yaml">
                  kube-prometheus-stack:
                  <br />
                  &nbsp;&nbsp;prometheus:
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;prometheusSpec:
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;scrapeConfigSelector:
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;matchLabels:
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;prometheus:
                  monitoring-prometheus
                </code>
              </pre>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The following are the parameters applied to the ScrapeConfig
                resource:
              </p>
              <pre className="mt-4 text-left h-68 w-full">
                <code className="language-yaml">
                  global:
                  <br />
                  &nbsp;&nbsp;scrapeconfig:
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;name: "grafana-tempo"
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;labels:
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;prometheus:
                  monitoring-prometheus
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;spec:
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;staticConfigs:
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;- targets:
                  &#91;'grafana-tempo.metrics.svc.cluster.local:3100'&#93;
                </code>
              </pre>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The Grafana Tempo Helm Chart does not provide the template for
                the ScrapeConfig resource, so this template has been added as
                part of the "metrics" Helm Chart.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Prometheus attaches the labels "job" and "instance"
                automatically to the scraped time series which serve to identify
                the target. In addition to the default labels, the example
                configurations add the label "cluster" with value "aks" to the
                scraped time series.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                [1]{" "}
                <a
                  className="underline"
                  href="https://artifacthub.io/packages/helm/prometheus-community/kube-prometheus-stack"
                  target="_blank"
                  rel="noreferrer"
                >
                  kube-prometheus-stack
                </a>
                <br />
                [2]{" "}
                <a
                  className="underline"
                  href="https://artifacthub.io/packages/helm/grafana/tempo"
                  target="_blank"
                  rel="noreferrer"
                >
                  grafana/tempo
                </a>
                <br />
                [3]{" "}
                <a
                  className="underline"
                  href="https://prometheus-operator.dev"
                  target="_blank"
                  rel="noreferrer"
                >
                  prometheus-operator
                </a>
              </p>
            </div>
          </div>
          <div className="mt-8 flex items-center text-left">
            <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
            {tags.map((item, index) => (
              <span
                key={`tag-${index}`}
                className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
              >
                {item}
              </span>
            ))}
          </div>
        </div>
        <div className="mb-16"></div>
        <div></div>
      </div>
      <Footer></Footer>
    </div>
  );
}

export default Post22;
