import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import Navigation from "../Navigation";
import Footer from "../Footer";
import Prism from "prismjs";
import "prismjs/themes/prism-twilight.css";
import "prismjs/components/prism-kotlin";
import { CalendarDateRangeIcon, TagIcon } from "@heroicons/react/24/outline";

function Post13() {
  const location = useLocation();
  const themeModeInitial = location?.state?.themeMode
    ? location.state.themeMode
    : "light";
  const [themeMode, setThemeMode] = useState(themeModeInitial);
  const tags = ["active-recall", "spring-boot", "kotlin"];

  const toggleThemeMode = () => {
    if (themeMode === "light") {
      setThemeMode("dark");
    } else {
      setThemeMode("light");
    }
  };

  useEffect(() => {
    if (location?.state?.themeMode) {
      setThemeMode(location.state.themeMode);
    }
    window.scrollTo(0, 0);
    Prism.hooks.add("before-highlight", function (env) {
      env.code = env.element.innerText;
    });
    Prism.highlightAll();
  }, [location]);

  return (
    <div className={themeMode === "dark" ? "dark" : "light"}>
      <Navigation
        themeMode={themeMode}
        onThemeChange={toggleThemeMode}
      ></Navigation>
      <div className="flex flex-col items-center content min-h-[calc(100vh-172px)] dark:bg-slate-custom1">
        <div className="w-11/12 sm:w-11/12 md:w-9/12 lg:w-8/12 xl:w-6/12 2xl:w-4/12 mx-0">
          <div className="mt-16 mb-4 flex justify-items-start content">
            <p className="font-semibold text-left text-lg lg:text-2xl w-full md:w-full dark:text-gray-custom1">
              Backend Web Application with Kotlin
            </p>
            <div className="line-break"></div>
            <span className="flex items-center">
              <CalendarDateRangeIcon className="h-6 w-6 stroke-orange-400 mr-2 text-gray-custom3 dark:text-gray-400" />
              <time className="text-gray-custom2 text-left text-sm lg:text-base w-full md:w-full dark:text-gray-custom4">
                Sep 9, 2024
              </time>
            </span>
            <div className="line-break"></div>
            <div className="articleBody">
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Kotlin is a concise and versatile JVM based language created by
                JetBrains. I used Kotlin for developing many different services
                and backend web applications.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Kotlin is a statically-typed programming language and is fully
                interoperable with Java [1]. The build tools Maven and Gradle
                fully support Kotlin, and these tools help managing the
                configuration of the libraries and the build process of the
                application.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Kotlin provides many features and concepts, such as null safety
                and extension functions, and it allows software developers to
                create concise and expressive code.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                In this article I will describe some technical aspects of the
                Active recall learning web application that I developed using
                Spring Boot 2 and Kotlin.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                Spring Boot and Kotlin make it really easy to create Rest
                controllers, services, entities, and interacting with the
                database.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The following is an example of a Rest Controller:
              </p>
              <pre className="mt-4 text-left h-68 w-full">
                <code className="language-kotlin">
                  &#64;RestController
                  <br />
                  &#64;RequestMapping("/api/workspaces")
                  <br />
                  class WorkspaceController(private val workspaceService:
                  WorkspaceService) &#123;
                  <br />
                  &nbsp;&nbsp;&#64;PostMapping(consumes =
                  [MediaType.APPLICATION_JSON_VALUE])
                  <br />
                  &nbsp;&nbsp;fun create(
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&#64;RequestBody request:
                  WorkspaceCreateRequest
                  <br />
                  &nbsp;&nbsp;): ResponseEntity&lt;Workspace&gt; &#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;val workspace =
                  workspaceService.create(request)
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;val builder =
                  ResponseEntity.status(HttpStatus.CREATED)
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;builder.location(URI("/api/workspaces/$&#123;workspace.id&#125;"))
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;return builder.body(workspace)
                  <br />
                  &nbsp;&nbsp;&#125;
                  <br />
                  &#125;
                </code>
              </pre>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The following is an example of a Service:
              </p>
              <pre className="mt-4 text-left h-68 w-full">
                <code className="language-kotlin">
                  &#64;Service
                  <br />
                  class WorkspaceService(
                  <br />
                  &nbsp;&nbsp;private val workspaceRepository:
                  WorkspaceRepository,
                  <br />
                  &nbsp;&nbsp;private val workspaceIdGenerator:
                  WorkspaceIdGenerator
                  <br />
                  ) &#123;
                  <br />
                  &nbsp;&nbsp;fun create(workspaceCreateRequest:
                  WorkspaceCreateRequest): Workspace &#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;val workspaceId =
                  workspaceIdGenerator.create()
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;val workspace =
                  Workspace.create(workspaceId, workspaceCreateRequest)
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;val entity =
                  WorkspaceEntity.create(workspace)
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;return
                  Workspace.create(workspaceRepository.save(entity))
                  <br />
                  &nbsp;&nbsp;&#125;
                  <br />
                  &#125;
                </code>
              </pre>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The following is an example of an Entity, defined as a data
                class:
              </p>
              <pre className="mt-4 text-left h-68 w-full">
                <code className="language-kotlin">
                  &#64;Entity
                  <br />
                  &#64;Table(name = "workspaces")
                  <br />
                  data class WorkspaceEntity(
                  <br />
                  &nbsp;&nbsp;&#64;Id val id: String = "",
                  <br />
                  &nbsp;&nbsp;val name: String = ""
                  <br />
                  ) &#123;
                  <br />
                  &nbsp;&nbsp;companion object &#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;fun create(workspace: Workspace) :
                  WorkspaceEntity &#123;
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;return
                  WorkspaceEntity(workspace.id, workspace.name)
                  <br />
                  &nbsp;&nbsp;&nbsp;&nbsp;&#125;
                  <br />
                  &nbsp;&nbsp;&#125;
                  <br />
                  &#125;
                </code>
              </pre>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The following extension function is used for converting a Java
                Optional&lt;T&gt; to a Kotlin nullable T:
              </p>
              <pre className="mt-4 text-left h-25 w-full">
                <code className="language-kotlin">
                  import java.util.Optional
                  <br />
                  fun &lt;T : Any&gt; Optional&lt;T&gt;.toNullable(): T? =
                  this.orElse(null)
                </code>
              </pre>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The business logic of the application is simple to follow, and I
                found the code written in Kotlin easy to read. The features
                provided by the Kotlin language, such as data classes, default
                arguments, and expressions, help to reduce the boilerplate code.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                I used Spring Security with JWT for managing the user
                authentication and authorization in the web application. I
                created the REST API documentation with OpenAPI, compliant with
                Swagger and Postman.
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                The GitHub repository with the web application is the following:
                <br />
                <a
                  className="underline"
                  href="https://github.com/sitMCella/learn-tool"
                  target="_blank"
                  rel="noreferrer"
                >
                  sitMCella/learn-tool
                </a>
              </p>
              <p className="leading-7 mt-4 text-left text-base lg:text-lg prose dark:prose-dark dark:prose-invert w-full">
                [1]{" "}
                <a
                  className="underline"
                  href="https://kotlinlang.org/docs/home.html"
                  target="_blank"
                  rel="noreferrer"
                >
                  kotlinlang.org
                </a>
              </p>
            </div>
          </div>
          <div className="mt-8 flex items-center text-left">
            <TagIcon className="h-5 w-5 stroke-orange-400 mr-1 text-gray-custom3 dark:text-gray-400" />
            {tags.map((item, index) => (
              <span
                key={`tag-${index}`}
                className="m-1 px-1.5 py-0.5 border rounded border-gray-custom3 text-gray-custom2 dark:border-gray-400 dark:text-gray-custom4 text-xs lg:text-sm font-medium text-left text-base prose"
              >
                {item}
              </span>
            ))}
          </div>
        </div>
        <div className="mb-16"></div>
        <div></div>
      </div>
      <Footer></Footer>
    </div>
  );
}

export default Post13;
